<!--流量统计里的form表单-->
<template>
    <div class="form-in-traffic-analysis">
        <el-form>
            <el-form-item label="时间">
                <el-date-picker
                    v-model="startTime"
                    type="datetime"
                    placeholder="选择起始日期时间"
                    align="right"
                    value-format="yyyy-MM-dd HH:mm:ss"
                    :picker-options="pickerOptions"
                    size="mini">
                </el-date-picker>
                <el-date-picker
                    v-model="endTime"
                    type="datetime"
                    placeholder="选择结束日期时间"
                    align="right"
                    value-format="yyyy-MM-dd HH:mm:ss"
                    :picker-options="pickerOptions"
                    size="mini">
                </el-date-picker>
            </el-form-item>
            <label class="label-in-event-search">统计目标</label>
            <el-form-item>
                <el-select v-model="sourceValue" clearable placeholder="请选择统计目标" size="small" class="event-source-selector">
                    <el-option
                        v-for="item in sourceOptions"
                        :key="item.sourceValue"
                        :label="item.sourceLabel"
                        :value="item.sourceValue">
                    </el-option>
                </el-select>
<!--                <button v-on:click="sourceValueLog">打印一下</button>-->
            </el-form-item>
            <el-form-item>
                <el-tree
                    :data="treeData"
                    show-checkbox
                    node-key="id"
                    ref="tree"
                    :default-expanded-keys="[2, 3]"
                    :default-checked-keys="[]"
                    :props="defaultProps">
                </el-tree>
            </el-form-item>
            <el-button type="danger" size="mini" class="submit-button-event-search" v-on:click="submit">搜索</el-button>
        </el-form>
    </div>
</template>

<script>

  import {request} from "../../../network/request";
  //TODO:这里本意不应该引入axios模块，应该在request里封装好post请求，但是先这么做吧

  export default {

        name: "FormInTrafficAnalysis",
        data() {
            return {

                //给树形菜单的数据，但是这里有一个问题 TODO:就是目前不能同时选择两个，甚至同时选择多个，不合理需要迭代
                treeData: [{
                    id: 1,
                    label: 'SIS',
                    children: [{
                        id: 2,
                        label: '电捕后煤气含氧量(arsa_41301)',
                    },{
                        id: 3,
                        label: '入管式炉煤气压力(prcsa_4201)',
                    },{
                        id: 4,
                        label: '脱苯塔顶压力(pisa_4204)',
                    }]
                }, {
                    id: 5,
                    label: '气体报警',
                }, {
                    id: 6,
                    label: '罐区',
                }],
                defaultProps: {
                    children: 'children',
                    label: 'label'
                },

                //给时间选择器的数据
                pickerOptions: {
                    shortcuts: [{
                        text: '今天',
                        onClick(picker) {
                            picker.$emit('pick', new Date());
                        }
                    }, {
                        text: '昨天',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24);
                            picker.$emit('pick', date);
                        }
                    }, {
                        text: '一周前',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', date);
                        }
                    }]
                },
                sourceOptions: [{
                    sourceValue: 'option_sis',
                    sourceLabel: 'SIS点'
                }, {
                    sourceValue: 'option_gas',
                    sourceLabel: '气体报警点'
                }, {
                    sourceValue: 'option3',
                    sourceLabel: '摄像头点'
                }, {
                    sourceValue: 'option4',
                    sourceLabel: '设备点'
                }, {
                    sourceValue: 'option5',
                    sourceLabel: '分组'
                }],
                sourceValue: '',

                statusOptions: [{
                    statusValue: '选项1',
                    statusLabel: '已恢复'
                }, {
                    statusValue: '选项2',
                    statusLabel: '未恢复'
                }],
                statusValue: '',

                // 这是给时间选择器的变量
                startTime: '',
                endTime: '',


                value3: '',
                radio: '1',
                checkListeventlevel:['选中且禁用','复选框 A'],
            };
        },
        methods: {
            //form表单中提交之后，记录表单项并且通过post请求发给后端
            submit() {
                //给post请求使用的数据
                let params = {
                    startTime: this.startTime, //开始时间
                    endTime: this.endTime, //结束时间
                    node: this.$refs.tree.getCheckedNodes(), //树控件内选择的点
                };
                this.$eventBus.emit("FormToHAMP", params);
                // axios.post(
                //     "http://127.0.0.1:8000/data_analysis_traffic_search", params
                // ).then(res => {
                //     this.$eventBus.emit("FormInTrafficAnalysis", res.data);
                // }).catch(err => {
                //         // eslint-disable-next-line no-console
                //         console.log(err)
                // });
            },
        },
        watch: {
            //检测到sourceValue变动，执行methods里的请求函数 TODO:这是给动态赋值树控件准备的，迭代时使用
            sourceValue:function (val) {
                // 请求sis列名列表
                if(val === 'option_sis') {
                    request({
                        url: "/sis_column_list/",
                     //   methods: "post",
                    }).then(res=>{
                        // eslint-disable-next-line no-console
                        //console.log(res)
                        this.data = res;
                        //
                    }).error(err=>{
                        // eslint-disable-next-line no-console
                        console.log(err)
                    })
                    // 请求gas列名列表
                }else if (val === 'option_gas') {
                    request({
                        url: "http://127.0.0.1:8000/gas_column_list/",
                     //   methods: "post",
                    }).then(res=>{
                        // eslint-disable-next-line no-console
                        console.log(res)
                    }).error(err=>{
                        // eslint-disable-next-line no-console
                        console.log(err)
                    })
                }
            },

            //监听时间的变化，work，说明拿到了
            // startTime:function (val) {
            //     // eslint-disable-next-line no-console
            //     console.log(val)
            // },
            // endTime:function (val) {
            //     // eslint-disable-next-line no-console
            //     console.log(val)
            // },

            //监听选点树变化
            // data:function (val) {
            //     // eslint-disable-next-line no-console
            //     console.log(val)
            // },
        },
        created() {

        },
    };
</script>

<style>
    .el-form-item__label {
        color:white;
    }
    .label-in-event-search {
        color: white;
    }
    .form-in-traffic-analysis {
        padding: 4px;
    }
    .submit-button-event-search {
        width: 209px;
        height: 30px;
        font-family: 幼圆;
        border-radius: 2px;
    }
    .el-tree {
        background: #303133;
        color: #e5e5e5;
    }
</style>

