import axios from "axios"

export function request(config) {

    const instance = axios.create({
    baseURL: "http://127.0.0.1:8000",
    timeout: 5000,
    });

    instance.interceptors.request.use(config => {
        // eslint-disable-next-line no-console
        // console.log(config);
        //这里拦截了之后要返回，不然的话其实请求并没有发出去，在发出的过程中被拦截了
        //每次发送请求的时候，在界面上显示一个loading圆圈
        //请求的时候需要携带特殊信息，比如登录信息，如果没有信息，跳转或提示或form添加信息，登录或什么
        return config;
    }, error => {
        //请求发不出去的时候
        // eslint-disable-next-line no-console
        console.log(error);
    });

    instance.interceptors.response.use(res => {
        //
        // eslint-disable-next-line no-console
        // console.log(res);
        return res.data;
    }, error => {
        // eslint-disable-next-line no-console
        console.log(error);
    });

    //这个地方可以这么写，是因为axios在create的时候，生成了一个Promise对象
    return instance(config)
}

