<!--我要在这里引入一个完整的echart-->
<template>
    <div class="history-in-traffic">
<!--        创建用于显示图表的dom,动态赋值长宽-->
        <div id="historyChart" :style="{width: '100%', height: '400px'}"></div>
    </div>
</template>

<script>
    import {drawEcharts} from "../../../../functionaljs/drawEcharts";
    import axios from 'axios';

    export default {
        name: "HistoryInTraffic",
        data() {
            return {
                dataSeriesForHistory: [], //请求回来的原始数据(时间，数据)
            }
        },
        props: {

        },
        // watch内监视原视数据变化，如变化，说明请求成功返回数据，执行画图操作，并把变化后值传入画图函数
        watch: {
            dataSeriesForHistory: function (val) {
                drawEcharts('historyChart', 'bar', '测点历史图表', val);
            }
        },
        methods: {},
        mounted() {
            // 这里面需要注意的是要改变this的指向，在this.$eventBus之后，this的指向会被改变，要提前保存指向data的this
            // eslint-disable-next-line no-console
            console.log("历史图被建立");
            drawEcharts('historyChart', 'bar', '测点历史图表', this.dataSeriesForHistory);
            let that = this;
            //拿到form数据
            this.$eventBus.on("FormToHAMP", function (data) {
                // 用这个数据做网络请求，然后用请求回来的结果划线，先打印看看是不是能拿到
                // eslint-disable-next-line no-console
                // console.log(data);
                axios.post(
                    "http://127.0.0.1:8000/data_analysis_traffic_search/", data
                ).then(res => {
                    that.dataSeriesForHistory = res.data;
                }).catch(err => {
                    // eslint-disable-next-line no-console
                    console.log(err)
                });
                //这个地方保留是因为接下来我要封装axios接口，然后使用自己的接口
            });
        },
    }
</script>

<style scoped>

</style>
