import echarts from 'echarts';
import customdark from '../../src/assets/json/customdark';


// 'divName' 'chartType' 'chartName'
export function drawEcharts(divName, chartType, chartName, data) {

    // xData, seriesData
    let xData=[];
    let seriesData=[];

    for (let key in data) {
        if(data.hasOwnProperty(key)) {
            xData.push(key);
            seriesData.push(data[key]);
        }
    }

    // eslint-disable-next-line no-console
    console.log(xData);
    // eslint-disable-next-line no-console
    console.log(seriesData);

    let obj = customdark;
    echarts.registerTheme('customdark', obj);
    let chart = echarts.init(document.getElementById(divName), 'customdark');

    let option = {
        title: {
            text: chartName,
            left: 10
        },
        toolbox: {
            feature: {
                dataZoom: {
                    yAxisIndex: false
                },
                saveAsImage: {
                    pixelRatio: 2
                }
            }
        },
        tooltip: {
            trigger: 'axis',
            axisPointer: {
                type: 'shadow'
            }
        },
        grid: {
            bottom: 90
        },
        dataZoom: [{
            type: 'inside'
        }, {
            type: 'slider'
        }],
        xAxis: {
            data: xData,
            silent: false,
            splitLine: {
                show: false
            },
            splitArea: {
                show: false
            }
        },
        yAxis: {
            splitArea: {
                show: false
            }
        },
        series: [{
            type: chartType,
            data: seriesData,
            // Set `large` for large data amount
            large: true
        }]
    };
    chart.setOption(option);
}
