<template>
    <div class="content-in-traffic-analysis">
        <el-row class="first-row">
            <el-radio-group v-model="radio" size="small">
                <!--                我现在知道click.native是个深坑-->
                <el-radio-button label="history" @click.native="handleChangeView('HistoryInTraffic')">
                    <strong><i class="fa fa-line-chart" aria-hidden="true"></i> 历史趋势</strong>
                </el-radio-button>
                <el-radio-button label="alarm" @click.native="handleChangeView('AlarmInTraffic')">
                    <strong><i class="fa fa-align-left" aria-hidden="true"></i> 报警统计</strong>
                </el-radio-button>
                <el-radio-button label="movement" @click.native="handleChangeView('MovementInTraffic')">
                    <strong><i class="fa fa-exchange" aria-hidden="true"></i> 移动平均</strong>
                </el-radio-button>
                <el-radio-button label="percentage" @click.native="handleChangeView('PercentageInTraffic')">
                    <strong><i class="fa fa-pie-chart" aria-hidden="true"></i> 报警比例</strong>
                </el-radio-button>
            </el-radio-group>
        </el-row>
        <el-row class="second-row">
            <component :is="currentView"></component>
        </el-row>
    </div>
</template>

<script>

    import AlarmInTraffic from "./chartintrafficcontent/AlarmInTraffic";
    import HistoryInTraffic from "./chartintrafficcontent/HistoryInTraffic";
    import MovementInTraffic from "./chartintrafficcontent/MovementInTraffic";
    import PercentageInTraffic from "./chartintrafficcontent/PercentageInTraffic";

    export default {
        name: 'ContentInTrafficAnalysis',
        components: {
            // 'v-chart': ECharts,
            AlarmInTraffic,
            HistoryInTraffic,
            MovementInTraffic,
            PercentageInTraffic,
        },
        data () {
            return {
                //默认的选项是从v-model里绑定的，click事件之后会改变
                radio: 'history',
                //默认是history图，和radio一致
                currentView:'HistoryInTraffic',
            }
        },
        methods: {
            handleChangeView:function(component){
                this.currentView = component;
            }
        },
    }
</script>

<style scoped>
    .content-in-traffic-analysis {
        min-height: 600px;
        padding: 2px;
    }
    .my-v-chart {
        width: 100%;
        min-height: 60%;
    }
    .el-tabs {
        height: 26px;
    }
    .el-tabs-pane {
        height: 100%;
    }
    .first-row {
        text-align: center;
    }
</style>
