<template>
    <div class="percentage-in-traffic">
        <div id="percentageChart" :style="{width: '100%', height: '400px'}"></div>
    </div>
</template>

<script>
    // 引入自定义的echart背景,为了将echart背景与软件背景相同
    import customdark from '../../../../../src/assets/json/customdark';
    import axios from 'axios';
    // import {request} from "../../../../network/request";

    export default {
        name: "PercentageInTraffic",
        data() {
            return {
                // mock data
                xDataForAlarm: [], //x轴数据
                seriesForAlarm: [], //series数据
                dataSeriesForAlarm: [], //请求回来的原始数据(时间，数据)

                // read data
                // xDataForLine: [], //x轴数据
                // seriesForLine: [], //series数据
                // dataSeriesForLine: [], //请求回来的原始数据(时间，数据)
            }
        },
        props: {

        },
        // watch内监视原视数据变化，如变化，说明请求成功返回数据，执行画图操作，并把变化后值传入画图函数
        watch: {
            dataSeriesForAlarm: function (val) {
                this.drawAlarm(val)
            }
        },
        methods: {
            //TODO:画图函数，这里需要详细配置一下，这个函数还有一个问题是当没有数据的时候，应该画一张空表
            drawAlarm(val){
                //这是数据预处理部分，将原视数据拆分x与series
                for(let key in val) {
                    if(val.hasOwnProperty(key)) {
                        this.xDataForAlarm.push(key);
                        this.seriesForAlarm.push(val[key]);
                    }
                }

                let obj = customdark;
                this.$echarts.registerTheme('customdark',obj);
                //初始化图表
                let alarmChart = this.$echarts.init(document.getElementById('percentageChart'),'customdark');
                //option传入图表所有配置项
                let option = {
                    title: {
                        text:  '测点报警记录',
                        left: 10
                    },
                    toolbox: {
                        feature: {
                            dataZoom: {
                                yAxisIndex: false
                            },
                            saveAsImage: {
                                pixelRatio: 2
                            }
                        }
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'shadow'
                        }
                    },
                    grid: {
                        bottom: 90
                    },
                    dataZoom: [{
                        type: 'inside'
                    }, {
                        type: 'slider'
                    }],
                    xAxis: {
                        data: this.xDataForAlarm,
                        silent: false,
                        splitLine: {
                            show: false
                        },
                        splitArea: {
                            show: false
                        }
                    },
                    yAxis: {
                        splitArea: {
                            show: false
                        }
                    },
                    series: [{
                        type: 'line',
                        data: this.seriesForAlarm,
                        // Set `large` for large data amount
                        large: true
                    }]
                };
                alarmChart.setOption(option);

                //将x与series置空
                this.xDataForAlarm.length = 0;
                this.seriesForAlarm.length = 0;
            }
        },
        mounted() {
            // 这里面需要注意的是要改变this的指向，在this.$eventBus之后，this的指向会被改变，要提前保存指向data的this
            // eslint-disable-next-line no-console
            console.log("报警图被建立");
            this.drawAlarm(this.dataSeriesForAlarm);
            let that = this;
            //拿到form数据
            this.$eventBus.on("FormToHAMP", function (data) {
                // 用这个数据做网络请求，然后用请求回来的结果划线，先打印看看是不是能拿到
                // eslint-disable-next-line no-console
                // console.log(data);
                axios.post(
                    "http://127.0.0.1:8000/data_analysis_alarm_search/", data
                ).then(res => {
                    that.dataSeriesForAlarm = res.data;
                }).catch(err => {
                    // eslint-disable-next-line no-console
                    console.log(err)
                });
                //这个地方保留是因为接下来我要封装axios接口，然后使用自己的接口
            });




            // let that = this;
            // //监听事件总线，获取数据
            // this.$eventBus.on("FormInTrafficAnalysis", function(data) {
            //         that.dataSeriesForLine = data;
            //     },
            //     this
            // );
        },
    }
</script>

<style scoped>

</style>
